import React, {useState} from 'react';
import styles from "./Paginator.module.css";
import cn from "classnames"

const Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 5}) => {
  let pagesCount = Math.ceil(totalItemsCount / pageSize);
  let pages = [];
  for (let i = 1; i <= pagesCount; i++) pages.push(i);
  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionNumber = (portionNumber - 1) * portionSize + 1;
  let rightPortionNumber = portionNumber * portionSize;


  return (
    <div className={cn(styles.pagePagination, styles.pagePagination)}>
      {portionNumber > 1 && <button
        onClick={() => {
          setPortionNumber(portionNumber - 1)
        }}
        className={cn(styles.nav, styles.navPrev)}
      >prev</button>}
      {pages
        .filter(p => p >= leftPortionNumber && p <= rightPortionNumber)
        .map(p => <span key={p} onClick={() => {
          onPageChanged(p)
        }} className={cn(styles.page, {
          [styles.selectedPage]: currentPage === p
        })}>{p}</span>)}
      {portionCount > portionNumber && <button
        onClick={() => {
          setPortionNumber(portionNumber + 1)
        }}
        className={cn(styles.nav, styles.navNext)}
      >next</button>}
    </div>
  );
};

export default Paginator;